.projects-container{
    padding: 30px 0px;
    display: grid;
    grid-auto-rows: 400px;
    width: 100%;
    grid-gap: 50px;
    grid-template-columns: 400px 400px;
    justify-content: center;
}
.project-tile{
    background-color: lightgray;
}
@media only screen and (max-width:1300px){
    .projects-container{
        grid-template-columns: 400px;
    }
}