.profiles-container{
    display: grid;
    grid-template-columns: 100px 100px 100px 100px 100px 100px;
    grid-auto-rows: 100px;
    justify-content: center;
    padding-top: 50px;
    grid-gap : 50px;
    width: 100%;
}
.profiles-unit__text{
    float: left;
    font-size: 15px;
    margin-top: -15px;
    color: white;
    margin-top: 7px;
}
.profiles-unit{
    padding: 35px;
    text-align: center;
    display:table-row;
    border: 1px solid red;
    grid-area: span 1 / span 2;
}
.profiles-unit4{
    grid-column: 2 / span 2;
}

.profiles-unit5{
    grid-column: 4 / span 2;
}

.profiles-icon{
    margin-right: 10px;
    float: left;
}